@import '../variables';

.wrapper {
  max-width: 1465px;
  margin: 0 auto;
}

.container {
  position: relative;
  height: 591px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  padding-left: 129px;
}

.header1 {
  width: 375px;
  font-size: 60px;
  margin-top: 105px;
  margin-bottom: 51px;
}

.par1 {
  width: 330px;
  margin-bottom: 18px;
}

.par2 {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-bottom: 43px;
}

